.post {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--twitter-background);
  padding-bottom: 10px;
}

.post-header-text > h3 {
  font-size: 15px;
  margin-bottom: 5px;
}

.post-header-description {
  margin-bottom: 10px;
  font-size: 15px;
}

.post-body {
  flex: 1;
  padding: 10px;
}

.post-body > img {
  border-radius: 20px;
}

.post-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.post-badge {
  font-size: 14px !important;
  color: var(--twitter-color) !important;
}

.post-header-special {
  font-weight: 600;
  font-size: 12px;
  color: gray;
}

.post-avatar {
  padding: 20px;
}
